@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);

body {
  font-family: "Raleway", sans-serif;
  color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(18, 21, 39, 0.86);
  margin: 0;
  width: 100%;
  height: 70vh;
}

.graph {
  color: antiquewhite; 
  background-color: rgb(6, 5, 19);
  border-radius: 12px;
}

.nodes {
  color: #fff;
}

.Main {
  width: 100%;
  height: 100vh;
  overflow: scroll;
}

.main-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  border-radius: 15px;
  margin: 10px;
}

.graph-box {
  padding: 10px;
  min-height: 400px;
  max-height: 599px;
  overflow: hidden;
  background: #1f1d2b;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  margin: 10px;
  width: 50%;
}

.hidden {
  display: none;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

.footer{
  padding: 40px 30px 40px;
  text-align: right;
  display: block;
}

.footer a {
  color: #fff;
  text-decoration: none;
}

@media screen and (max-width: 999px) {
  .graph-box,
  .main-container {
    width: 95%;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
  }
}

.article-wrapper {
    min-height: 400px;
    max-height: 599px;
    width: 50%;
    padding: 10px;
    background: #1f1d2b;
    display: relative;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    margin: 10px;
    overflow: scroll;
  }

  .article-content {
    margin-top: auto;
    margin:10px;
    padding: 10px;
  }

  a {
      color: #fff;
  }

  a:link {
    color: #87CEFA;
  }

  @media screen and (max-width: 999px) {
   
    .article-wrapper {
      width: 95%;
      margin-left: auto;
      margin-right: auto;
    }
    
  }
.loader {
    color: #ffffff;
    font-size: 4px;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    position: relative;
    right: 80px;
    text-indent: -9999em;
    -webkit-animation: load4 1.3s infinite linear;
    animation: load4 1.3s infinite linear;
    transform: translateZ(0);
  }
  @-webkit-keyframes load4 {
    0%,
    100% {
      box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
    }
    12.5% {
      box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    25% {
      box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    37.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    50% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    62.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
    }
    75% {
      box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
    }
    87.5% {
      box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
    }
  }
  @keyframes load4 {
    0%,
    100% {
      box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
    }
    12.5% {
      box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    25% {
      box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    37.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    50% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    62.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
    }
    75% {
      box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
    }
    87.5% {
      box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
    }
  }
  
.search-container {
  padding: 20px;
  background: #1f1d2b;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  margin: 20px;
}

.search-bar {
  top: 10px;
  left: 10px;
  display: flex;
  width: 100%;
  max-width: 999px;
  padding: 1px;
  text-align: centre;
}

.search-bar input {
  width: 100%;
  height: 54px;
  border: none;
  background-color: #353340;
  border-radius: 8px;
  font-size: 25px;
  font-weight: 500;
  margin: 0;
  padding-left: 14px;
  box-shadow: 0 0 0 2px rgba(134, 140, 160, 0.02);

  color: #fff;
  float: left;
}

.search-bar input:focus,
textarea:focus,
select:focus {
  outline: none;
}

.red-alert {
  border: 3px solid red;
  border-radius: 8px;
}

@media screen and (max-width: 999px) {
  ::-webkit-input-placeholder {
    font-size: 0.8em;
  }
  :-ms-input-placeholder {
    font-size: 0.8em;
  }
  ::-ms-input-placeholder {
    font-size: 0.8em;
  }
  ::placeholder {
    font-size: 0.8em;
  }
}

.search-btn {
  float: left;
  width: 60px;
  padding: 10px;
  background: white;
  color: white;
  font-size: 15px;
  border: 0;
  cursor: pointer;
  border-radius: 8px;
}

.magnify:after {
  content: url("https://api.iconify.design/et-magnifying-glass.svg?height=32");
  vertical-align: -0.125em;
}

.search-btn:hover {
  background: #0b7dda;
}

  .topnav {
    background: #092756;
    color: #fff;
    overflow:hidden;
    margin: 0;
    display: flex;
  }

  .background-2 { 
    background: #092756;
    background: -webkit-radial-gradient(0% 100%, ellipse cover, rgba(104,128,138,.4) 10%,rgba(138,114,76,0) 40%), linear-gradient(to bottom, rgba(57,173,219,.25) 0%,rgba(42,60,87,.4) 100%), linear-gradient(135deg, #670d10 0%,#092756 100%);
  }

  .topnav a {
    float: right;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 20px 22px;
    text-decoration: none;
  }

  .topnav a:hover {
    background-color: #1f1d2b;
    color: white;
  }

  .title {
    font-family: "Righteous", cursive;
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    font-size: 1.25em;
    padding: 14px 16px;
  }

  .dropbtn {
    width: 115px;
    font-size: 1em;
    border: none;
    outline: none;
    padding: 20px 22px;
    color: #fff;
    background-color: inherit;
    font-family: inherit;
    margin: 0 5px 0 5px;
    display: block;
    align-items: center;
  }

  .navbar a:hover,
  .dropdown:hover .dropbtn {
    background-color: #1f1d2b;
    color: #fff;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #1f1d2b;
    color: #fff;
    width: 300px;
    align-items:flex-end;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
    border-radius: 4px;
    border: 4px;
    border-color: #fff;
  }

  .dropdown-content a {
    float: none;
    background-color: #fff;
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
  }

  .dropdown-content a:hover {
    background-color: #fff;
    color: black;
  }

  .dropdown-content:hover {
    background-color: #36324d;
    
  }

  .dropdown:hover .dropdown-content {
    display: block;
  }

  .layout-btn {
    float: right;
    overflow: hidden;
    font-size: 0.8em;
    border: none;
    outline: none;
    padding: 20px 22px;
    color: #fff;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
    display: block;
  }

  .layout-btn:hover {
    background-color: #fff;
    color: black;
  }
